












































































import {Provide, Watch, Component, Vue} from 'vue-property-decorator';
import {Action, State} from 'vuex-class';
import {deepEqual} from '@/domain/services/common/ObjectActions';
import {baseUrl, baseUrlAPI, dadataApiKey} from '@/globals';
import axios from 'axios';
import 'vue2-datepicker/index.css';
import 'vue2-datepicker/locale/ru';

@Component({
    components: {  },
})
export default class Events extends Vue {

    @Provide() private birthdayPersons = [];

    /**
     * Создание компонента
     */
    public async created() {
        this.getPersonBirthday();
    }

    /**
     * Получить именинников
     */
    public async getPersonBirthday() {
        try {
            const res = await axios.get(`${baseUrlAPI}person/birthday`);
            this.birthdayPersons = res.data;
        } catch {
            // ...
        }
    }

    /**
     * VIP-именниники
     */
    get resolveVIPBirthdayPersons() {
        return this.birthdayPersons.filter(person => person.status && person.status === 'vip');
    }

    /**
     * Именниники
     */
    get resolveBirthdayPersons() {
        return this.birthdayPersons.filter(person => person.status !== 'vip');
    }

    /**
     * Путь к фотографии
     */
    public resolvePhotoSrc(photo) {
        return (photo === '' || photo === null) ? '/images/social.png' : `${baseUrl}/storage/${photo}`;
    }

    /**
     * День рождения
     *
     * @param date
     */
    public resolvedDate(date) {
        let parseValue = date.split(' ');
        parseValue = parseValue[0].split('-');
        return `${parseValue[2]}.${parseValue[1]}.${parseValue[0]}`;
    }

    /**
     * Телефона
     *
     */
    public resolvedPhones(phones) {
        if (phones === null || phones === '' || phones === '"[]"') {
            return [];
        }
        return JSON.parse(JSON.parse(phones));
    }

}
